<template>
    <MySection title="portofolio">
        <div class="row align-items-center">
            <div v-for="(img,index) in imgs" :key="index" class="col-6 col-md-3 portofolio">
                <div class="image" @click="showLightbox(index)" :style="{ backgroundImage: 'url('+img.src+')'}">
                </div>
                <div class="d-flex">
                    <div v-for="(type, i) in img.type" :key="i" :class="['portofolio-badge', type]">
                        {{ type }}
                    </div>
                </div>
                <vue-easy-lightbox :imgs="img.imgs" :visible="img.lightbox" @hide="showLightbox(index)"></vue-easy-lightbox>
            </div>
        </div>
    </MySection>
</template>

<script>
import VueEasyLightbox from 'vue-easy-lightbox'
import MySection from './MySection.vue'

export default {
    name: 'Portofolio',
    components:{
        VueEasyLightbox,
        MySection
    },
    data() {
        return {
            imgs: [
                {
                    src: require('../assets/image/portofolio/kasir/kasir_1.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/kasir/kasir_1.png'),
                        require('../assets/image/portofolio/kasir/kasir_2.png'),
                        require('../assets/image/portofolio/kasir/kasir_3.png'),
                        require('../assets/image/portofolio/kasir/kasir_4.png'),
                        require('../assets/image/portofolio/kasir/kasir_5.png'),
                        require('../assets/image/portofolio/kasir/kasir_6.png'),
                        require('../assets/image/portofolio/kasir/kasir_7.png'),
                        require('../assets/image/portofolio/kasir/kasir_8.png'),
                        require('../assets/image/portofolio/kasir/kasir_9.png'),
                        require('../assets/image/portofolio/kasir/kasir_10.png'),
                        require('../assets/image/portofolio/kasir/kasir_11.png')
                    ],
                    type: ['laravel', 'jquery']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/clock.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/clock.png'),
                    ],
                    type: ['coreldraw']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/game_bot.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/game_bot.png'),
                    ],
                    type: ['coreldraw']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/game_boy.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/game_boy.png'),
                    ],
                    type: ['coreldraw']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/guitar.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/guitar.png'),
                    ],
                    type: ['coreldraw']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/sepatu.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/sepatu.png'),
                    ],
                    type: ['coreldraw']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/korek.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/korek.png'),
                    ],
                    type: ['coreldraw']
                },
                {
                    src: require('../assets/image/portofolio/coreldraw/ping_pong.png'),
                    lightbox: false,
                    imgs:[
                        require('../assets/image/portofolio/coreldraw/ping_pong.png'),
                    ],
                    type: ['coreldraw']
                }
            ]
        }
    },
    methods:{
        showLightbox(index) {
            this.imgs[index].lightbox = !this.imgs[index].lightbox
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/portofolio.scss';
</style>