<template>
<div>
    <section class="section container py-5">
        <div class="text-center pb-5">
            <h2>{{ title[0].toUpperCase() + title.slice(1).toLowerCase() }}</h2>
        </div>
        <slot></slot>
    </section>
</div>
</template>

<script>
import { globalStore } from '@/stores/globalStore';
import { mapActions, mapState } from 'pinia';

export default {
    name: 'MySection',
    props: ['title'],
    data() {
        return {
            offsetTop: 0,
            offsetBottom: 0,
            passed: false
        }
    },
    watch: {
        scroll(value) {
            if(value >= this.offsetTop && value <= this.offsetBottom && !this.passed) {
                this.$emit('scrolled')
            }
        }
    },
    computed:{
        ...mapState(globalStore, ['scroll'])
    },
    methods: {
        ...mapActions(globalStore, ['addNavbarItem'])
    },
    mounted() {
        this.offsetTop = this.$el.offsetTop
        this.offsetBottom = this.offsetTop + this.$el.getBoundingClientRect().height
        this.addNavbarItem({
            name: this.title.toUpperCase(),
            offsetTop: this.$el.offsetTop
        }) 
    }
}
</script>